	module.exports = {"data":{"markdownRemark":{"html":"<p>Sometimes it’s useful to see a log of all the actions and state updates. <strong>Tiny Atom</strong> ships with a built in console logger.</p>\n<p>Enable it like this:</p>\n<div class=\"gatsby-highlight\">\n      <pre class=\"language-js\"><code class=\"language-js\"><span class=\"token keyword\">const</span> createAtom <span class=\"token operator\">=</span> <span class=\"token function\">require</span><span class=\"token punctuation\">(</span><span class=\"token string\">'tiny-atom'</span><span class=\"token punctuation\">)</span>\n<span class=\"token keyword\">const</span> log <span class=\"token operator\">=</span> <span class=\"token function\">require</span><span class=\"token punctuation\">(</span><span class=\"token string\">'tiny-atom/log'</span><span class=\"token punctuation\">)</span>\n\n<span class=\"token keyword\">const</span> atom <span class=\"token operator\">=</span> <span class=\"token function\">createAtom</span><span class=\"token punctuation\">(</span>initialState<span class=\"token punctuation\">,</span> actions<span class=\"token punctuation\">,</span> <span class=\"token punctuation\">{</span> debug<span class=\"token punctuation\">:</span> log <span class=\"token punctuation\">}</span><span class=\"token punctuation\">)</span></code></pre>\n      </div>\n<p>Open browser’s console to see what actions are firing and what state updates are being applied.</p>\n<p>\n  <span\n    class=\"gatsby-resp-image-wrapper\"\n    style=\"position: relative; display: block; ; max-width: 786px; margin-left: auto; margin-right: auto;\"\n  >\n    <span\n      class=\"gatsby-resp-image-background-image\"\n      style=\"padding-bottom: 30.53053053053053%; position: relative; bottom: 0; left: 0; background-image: url('data:image/png;base64,'); background-size: cover; display: block;\"\n    >\n      <img\n        class=\"gatsby-resp-image-image\"\n        style=\"width: 100%; height: 100%; margin: 0; vertical-align: middle; position: absolute; top: 0; left: 0; box-shadow: inset 0px 0px 0px 400px #ffffff;\"\n        alt=\"Console Logger\"\n        title=\"\"\n        src=\"/static/console-logger-3-62f3b1c81d31d6f0d8f976230971d8f3-0e19c.png\"\n        srcset=\"/static/console-logger-3-62f3b1c81d31d6f0d8f976230971d8f3-a9803.png 197w,\n/static/console-logger-3-62f3b1c81d31d6f0d8f976230971d8f3-fcf18.png 393w,\n/static/console-logger-3-62f3b1c81d31d6f0d8f976230971d8f3-0e19c.png 786w,\n/static/console-logger-3-62f3b1c81d31d6f0d8f976230971d8f3-ab40d.png 1179w,\n/static/console-logger-3-62f3b1c81d31d6f0d8f976230971d8f3-c4dff.png 1572w,\n/static/console-logger-3-62f3b1c81d31d6f0d8f976230971d8f3-15f87.png 1998w\"\n        sizes=\"(max-width: 786px) 100vw, 786px\"\n      />\n    </span>\n  </span>\n  </p>","excerpt":"Sometimes it’s useful to see a log of all the actions and state updates.  Tiny Atom  ships with a built in console logger. Enable it like…","timeToRead":1,"frontmatter":{"title":"Console logger"},"parent":{"__typename":"File","relativePath":"console-logger.md"}}},"pathContext":{"slug":"/console-logger/"}}

